

.btn{
    background-color: transparent;
    border: none;
    color: antiquewhite;
    cursor: pointer;
    font-size: 13px;
    padding: 7px 10px 7px 10px;

    transition:color .3s ease;

    &:focus{

        outline: none;

    }
    &:hover{
        color: darken($color: white, $amount: 20);
    }
}

.btn-danger{
    background-color:red;
    width:200px;
    margin-left: auto;
    margin-right:auto;
    border-radius:10px;
    padding:15px;
    margin-top: 4%;
    margin-bottom: 4%;

    transition:background-color .5s  ease;

    &:hover{
        background-color:darken($color: red, $amount: 15)
    }

}


.btn-primary{
    background-color: $primary;
    border-radius: 3px;

    &:disabled{
        background-color: lighten($color: $primary, $amount: 15)

        }
    &:hover{
        background-color: darken($color: $primary, $amount:15)
    }
}
.btn-block{
    width: 100%;
}

.google-btn {
    cursor: pointer;
    margin-top: 5px;
    width: 100%;
    height: 42px;
    background-color: $google-blue;
    border-radius: 2px;
    box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.25);

    transition: box-shadow .3s ease;

    .google-icon-wrapper {
        position: absolute;
        margin-top: 1px;
        margin-left: 1px;
        width: 40px;
        height: 40px;
        border-radius: 2px;
        background-color: $white;
    }
    .google-icon {
        position: absolute;
        margin-top: 11px;
        margin-left: 11px;
        width: 18px;
        height: 18px;
    }
    .btn-text {
        float: right;
        margin: 11px 40px 0 0;
        color: $white;
        font-size: 14px;
        letter-spacing: 0.2px;
    }
    &:hover {
        box-shadow: 0 0 6px $google-blue;
    }
    &:active {
        background: $button-active-blue;
    }
}

