nd-color:#fff}.google-btn .google-icon{position:absolute;margin-top:11px;margin-left:11px;width:18px;height:18px}.google-btn .btn-text{float:right;margin:11px 40px 0 0;color:#fff;font-size:14px;letter-spacing:0.2px}.google-btn:hover{box-shadow:0 0 6px #4285f4}.google-btn:active{background:#1669f2}.link{color:#363636;text-decoration:none}.link:hover{text-decoration:underline}.journal__main-content{display:flex;height:100vh}.journal__sidebar{background-color:#363636;color:white;display:flex;flex-direction:column;height:100vh;padding-left:10px;padding-right:10px;width:50vh;position:relative;box-shadow:0 0 5px black}.journal__sidebar-navbar{display:flex;margin-bottom:20px;margin-top:-17px;justify-content:space-between}.journal__sidebar-navbar h3 span{font-weight:lighter;font-family:'M PLUS 2', sans-serif}.journal__new-mock{display:flex;flex-direction:column;justify-content:flex-end;cursor:pointer;align-items:center;margin-top:17px;margin-bottom:1px}.journal__new-entry{display:flex;flex-direction:column;cursor:pointer;align-items:center;justify-content:center;margin-bottom:5px;width:100%;transition:color .6s ease}.journal__new-entry:hover{color:#b2b2b2}.journal__entries{flex:1,1, 1 auto;height:60vh;margin-top:15px;overflow:scroll}.journal__entry{background-color:white;border-radius:4px;color:#363636;min-height:100px;justify-content:space-between;display:flex;margin-bottom:10px;overflow:hidden}.journal__entry-picture{display:flex;flex-direction:row;justify-content:center;margin-left:10px;margin-top:auto;margin-bottom:auto;height:9vh;width:10vw;border-radius:10px}.journal__entry-body{padding:10px}.journal__entry-title{font-size:16px;font-weight:bold}.journal__entry-content{font-size:14px}.journal__entry-date-box{display:flex;flex-direction:column;justify-content:center;align-items:center;margin-left:auto;margin-right:7px;font-family:'M PLUS 2', sans-serif}.journal__entry-date-box span{font-size:12px}.nothing__main-context{align-items:center;background-color:#4285f4;color:w